* {
	font-family: Arial, Helvetica, sans-serif;
}

body {
	padding: 60px;
}

input {
	color: grey;
	font-size: 1.2rem;
	width: 100%;
}

.cardGrid {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}

.card {
  background-color: lavender;
	display: flex;
	flex-direction: column;
	/* flex-wrap: wrap; */
	/* justify-content: center; */
	border: 1px solid grey;
	border-radius: 6px;
	margin: 5px;
	overflow: hidden;
  max-width: 200px;
}
.card p {
	padding: 5px;
	text-align: center;
}

.card img {
  align-self: center;
	max-height: 170px;
	border-radius: 3px;
	border: 1px solid grey;
}
